.coffee-ico-container {
  /* margin-top: 40px; */
  margin-bottom: 20px;
}

.mug-coffee {
  max-width: 100px;
  width: 100%;
}
.smoke-container {
  display: -webkit-flex;
  display: flex;
  width: 60px;
}
.smoke-1 {
  -webkit-animation: shift 5s linear 0.5s infinite;
          animation: shift 5s linear 0.5s infinite;
  opacity: 0;
}
.smoke-2 {
  -webkit-animation: shift-2 4s linear 0.6s infinite;
          animation: shift-2 4s linear 0.6s infinite;
  opacity: 0;
}
.smoke-3 {
  -webkit-animation: shift-3 5s linear 1.2s infinite;
          animation: shift-3 5s linear 1.2s infinite;
  opacity: 0;
}
.mug {
  width: 35px;
  height: 38px;
  border-radius: 8px;
  background-color: #fff;
  border: 4px solid #363636;
  position: relative;
  margin-left: 20px;
  /* box-shadow: 1px 3px 4px rgba(0, 0, 0, 0.2); */
}
.mug:after {
  content: "";
  position: absolute;
  z-index: -1;
  right: -15px;
  top: 15%;
  width: 35px;
  height: 20px;
  border: 4px solid #363636;
  border-radius: 15px;
  /* box-shadow: 1px 3px 4px rgba(0, 0, 0, 0.2); */
}
.smokes {
  -webkit-transform: translateX(35px);
          transform: translateX(35px);
}
@-webkit-keyframes shift {
  0% {
    -webkit-transform: translate(10px, 155%);
            transform: translate(10px, 155%);
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(10px, 0%);
            transform: translate(10px, 0%);
    opacity: 0;
  }
}
@keyframes shift {
  0% {
    -webkit-transform: translate(10px, 155%);
            transform: translate(10px, 155%);
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(10px, 0%);
            transform: translate(10px, 0%);
    opacity: 0;
  }
}
@-webkit-keyframes shift-2 {
  0% {
    -webkit-transform: translate(0, 155%);
            transform: translate(0, 155%);
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 0;
  }
}
@keyframes shift-2 {
  0% {
    -webkit-transform: translate(0, 155%);
            transform: translate(0, 155%);
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 0;
  }
}
@-webkit-keyframes shift-3 {
  0% {
    -webkit-transform: translate(-10px, 155%);
            transform: translate(-10px, 155%);
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(-10px, 0);
            transform: translate(-10px, 0);
    opacity: 0;
  }
}
@keyframes shift-3 {
  0% {
    -webkit-transform: translate(-10px, 155%);
            transform: translate(-10px, 155%);
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(-10px, 0);
            transform: translate(-10px, 0);
    opacity: 0;
  }
}

